@import '../../style/variables';

.character {
  z-index: 500;
  cursor: pointer;
  position: absolute;

  & > img {
    max-width: 100%;
    max-height: 100%;
  }

  &--name {
    color: $white;
    font-weight: bold;
    text-align: center;
  }
}


